/**
 * Tag 标签
 * @description 用于展示1个或多个文字标签，可点击切换选中、不选中的状态
 * @tutorial https://ext.dcloud.net.cn/plugin?id=35
 * @property {String} text 标签内容
 * @property {String} size = [normal|small] 大小尺寸
 *    @value normal 正常
 *    @value small 小尺寸
 * @property {String} type = [default|primary|success｜warning｜error｜royal]  颜色类型
 *    @value default 灰色
 *    @value primary 蓝色
 *    @value success 绿色
 *    @value warning 黄色
 *    @value error 红色
 *    @value royal 紫色
 * @property {Boolean} disabled = [true|false] 是否为禁用状态
 * @property {Boolean} inverted = [true|false] 是否无需背景颜色（空心标签）
 * @property {Boolean} circle = [true|false] 是否为圆角
 * @event {Function} click 点击 Tag 触发事件
 */

export default {
    name: 'UniTag',
    props: {
        type: {
            // 标签类型default、primary、success、warning、error、royal
            type: String,
            default: 'default'
        },
        size: {
            // 标签大小 normal, small
            type: String,
            default: 'normal'
        },
        // 标签内容
        text: {
            type: String,
            default: ''
        },
        disabled: {
            // 是否为禁用状态
            type: [Boolean, String],
            default: false
        },
        inverted: {
            // 是否为空心
            type: [Boolean, String],
            default: false
        },
        circle: {
            // 是否为圆角样式
            type: [Boolean, String],
            default: false
        },
        mark: {
            // 是否为标记样式
            type: [Boolean, String],
            default: false
        }
    },
    methods: {
        onClick () {
            if (this.disabled === true || this.disabled === 'true') {
                return
            }
            this.$emit('click')
        }
    }
}
